/*  ------------------------------------------------------------------------*/
/*  general styles
/*  ------------------------------------------------------------------------*/
@import url("../icons/font-awesome/css/font-awesome.min.css");
@import url("../icons/fontello/css/fontello.css");

body{
    background: #0f0f0f;
    margin:unset;
}

header, menu, section{
    display: block;
}


*,
*:before,
*:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
/*  ------------------------------------------------------------------------*/
/*  header section
/*  ------------------------------------------------------------------------*/

header {
    position: relative;
    height: 800px;
    min-height: 500px;
    width: 100%;
    background: #161415 url(../images/desktop-web-1.jpg) no-repeat top center;
    background-size: cover !important;
    -webkit-background-size: cover !important;
    text-align: center;
    overflow: hidden;
}

header:before{
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}

header .banner {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
}

header .banner-text { width: 100%; }

header .banner-text h1 {
    font: 90px/1.1em 'opensans-bold', sans-serif;
    color: #fff;
    letter-spacing: -2px;
    margin: 0 auto 18px auto;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, .8);
}

header .banner-text h3 {
    font: 20px/1.9em 'opensans-bold', sans-serif;
    color: #fff;
    margin: 0 auto;
    width: 70%;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, .5);
}

header .banner-text h3 span,
header .banner-text h3 a {
   color: #fff;
}
header .banner-text hr {
   width: 60%;
   margin: 18px auto 24px auto;
   border-color: #2F2D2E;
   border-color: rgba(150, 150, 150, .1);
}

/* header social links */
header .social {
   margin: 24px 0;
   padding: 0;
   font-size: 30px;
   text-shadow: 0px 1px 2px rgba(0, 0, 0, .8);
}
header .social li {
   display: inline-block;
   margin: 0 15px;
   padding: 0;
}
header .social li a { color: #fff; }
header .social li a:hover { color: #11ABB0; }

/* scrolldown link */
header .scrolldown a {
   position: absolute;
   bottom: 30px;
   left: 50%;
   margin-left: -29px;
   color: #fff;
   display: block;
   height: 42px;
   width: 42px;
   font-size: 42px;
   line-height: 42px;
   color: #fff;
   border-radius: 100%;

   -webkit-transition: all .3s ease-in-out;
   -moz-transition: all .3s ease-in-out;
   -o-transition: all .3s ease-in-out;
   transition: all .3s ease-in-out;
}

header .scrolldown a:not([href]):not([tabindex]){
    color:#fff;
}
header .scrolldown a:not([href]):not([tabindex]):hover { color: #11ABB0; }

/*  ------------------------------------------------------------------------*/
/*  navegation
/*  ------------------------------------------------------------------------*/

#navegation{
    font: 12px 'opensans-bold', sans-serif;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    margin: 0 auto;
    z-index: 100;
    position: fixed;
    left: 0;
    top: 0;
}

#navegation ul, #navegation li, #navegation a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

.opaque { background-color: #333; }

/* hide toggle button */
#navegation > button.mobile-btn { display: none; }

ul#nav {
    min-height: 48px;
    width: auto;
    text-align: center;
 }

ul#nav li {
    position: relative;
    list-style: none;
    height: 48px;
    display: inline-block;
}

ul#nav li a {

    /* 8px padding top + 8px padding bottom + 32px line-height = 48px */
    
    display: inline-block;
    padding: 8px 13px;
    line-height: 32px;
    text-decoration: none;
    text-align: left;
    color: #fff;
    
    -webkit-transition: color .2s ease-in-out;
    -moz-transition: color .2s ease-in-out;
    -o-transition: color .2s ease-in-out;
    -ms-transition: color .2s ease-in-out;
    transition: color .2s ease-in-out;
}
    
ul#nav li a:active { background-color: transparent !important; }
/* ul#nav li.current a { color: #F06000; } */

ul#nav li > .current{ color: #F06000; }

ul#nav li.cursorHover {
    cursor: pointer;
}

/*  Links  ------------------------------------------------------- */

a, a:visited {
    text-decoration: none;
    outline: 0;
    color: #11ABB0;
 
    -webkit-transition: color .3s ease-in-out;
    -moz-transition: color .3s ease-in-out;
    -o-transition: color .3s ease-in-out;
    transition: color .3s ease-in-out;
}
a:hover, a:focus { color: #313131; }
p a, p a:visited { line-height: inherit; }

/*  ------------------------------------------------------------------------*/
/*  about section
/*  ------------------------------------------------------------------------*/

#about {
    background: #2b2b2b;
    padding-top: 96px;
    padding-bottom: 66px;
    overflow: hidden;
}

#about a, #about a:visited  { color: #fff; }
#about a:hover, #about a:focus { color: #11ABB0; }

#about h2 {
    font: 22px/30px 'opensans-bold', sans-serif;
    color: #fff;
    margin-bottom: 12px;
}
#about p {
    line-height: 30px;
    color: #7A7A7A;
}
#about .profile-pic {
    position: relative;
    width: 240px;
    height: 240px;
    border-radius: 100%;
}
#about .contact-details { width: 41.66667%; }
#about .download {
    width: 58.33333%;
    padding-top: 6px;
}
#about .main-col { padding-right: 5%; }
#about .download .button {
    margin-top: 6px;
    background: #444;
}
#about .download .button:hover {
    background: #fff;
    color: #2B2B2B;
}
#about .download .button i {
    margin-right: 15px;
    font-size: 20px;
}

/*  ------------------------------------------------------------------------*/
/*  Experience section
/*  ------------------------------------------------------------------------*/


#experience {
    background: #fff;
    padding-top: 90px;
    padding-bottom: 72px;
    overflow: hidden;
}

#experience a, #experience a:visited  { color: #11ABB0; }
#experience a:hover, #experience a:focus { color: #313131; }

#experience h1 {
    font: 18px/24px 'opensans-bold', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
}
#experience h1 span {
    border-bottom: 3px solid #11ABB0;
    padding-bottom: 6px;
}
#experience h3 {
    font: 25px/30px 'opensans-bold', sans-serif;
}

#experience .header-col { padding-top: 9px; }
#experience .main-col { padding-right: 10%; }

.education, .work {
    margin-bottom: 48px;
    padding-bottom: 24px;
    border-bottom: 1px solid #E8E8E8;
}
#experience .info {
    font: 16px/24px 'librebaskerville-italic', serif;
    color: #6E7881;
    margin-bottom: 18px;
    margin-top: 9px;
}
#experience .info span {
    margin-right: 5px;
    margin-left: 5px;
}
#experience .date {
    font: 15px/24px 'opensans-regular', sans-serif;
    margin-top: 6px;
}


/*  ------------------------------------------------------------------------*/
/*  Portafolio
/*  ------------------------------------------------------------------------*/
#portfolio {
    background: #ebeeee;
    padding-top: 90px;
    padding-bottom: 60px;
 }

#portfolio h1 {
    font: 15px/24px 'opensans-semibold', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    margin-bottom: 48px;
    color: #95A3A3;
}
 
 /* Portfolio Content */
#portfolio-wrapper .columns { margin-bottom: 36px; }
.projects{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 110px 0 110px ;
}

/*  portfolio items     */
.project-item{
    display: flex;
	align-items: center;
	justify-content: center;
	/* flex-direction: column; */
	margin: 20px auto;
    margin: 0;
	overflow: hidden;
	border-radius: 10px;
    background-color: #fff;
    width: 100%;
}

.project-info{
    padding: 30px;
    flex-basis: 50%;
    height: 100%;
    display: block;
	align-items: flex-start;
	justify-content: center;
	flex-direction: column;
	background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
	color: white;
}

#portfolio .project-info h1{
    font-size: 4rem;
	font-weight: 500;
}

#portfolio .project-item:nth-child(even) {
    flex-direction: row-reverse;
    /* padding: inherit; */
    width: 100%;
    display: flex;
    justify-content: flex-start;
    
}

.project-carousel{
    padding: 0px;
    flex-basis: 50%;
	height: 300px;
	overflow: hidden;
	position: relative;
    list-style: none;
	width: 100%;
}

/*  ------------------------------------------------------------------------*/
/*  Contact Me
/*  ------------------------------------------------------------------------*/

#contact {
    background: #191919;
    padding-top: 96px;
    padding-bottom: 102px;
    color: #636363;
 }
 #contact .section-head { margin-bottom: 42px; }
 
 #contact a, #contact a:visited  { color: #11ABB0; }
 #contact a:hover, #contact a:focus { color: #fff; }
 
 #contact h1 {
    font: 18px/24px 'opensans-bold', sans-serif;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #EBEEEE;
    margin-bottom: 6px;
 }

 #contact h1 span { display: none; }
 #contact h1#icon-mail:before {
    font-family: 'FontAwesome';
    content: "\f0e0";
    padding-right: 10px;
    font-size: 72px;
    line-height: 72px;
    text-align: left;
    float: left;
    color: #ebeeee;
 }
 
 #contact h4 {
    font: 16px/24px 'opensans-bold', sans-serif;
    color: #EBEEEE;
    margin-bottom: 6px;
 }
 #contact p.lead {
    font: 18px/36px 'opensans-light', sans-serif;
    padding-right: 3%;
 }
 #contact .header-col { padding-top: 6px; }
 
 
 /* contact form */
 #contact form { margin-bottom: 30px; }
 #contact label {
    font: 15px/24px 'opensans-bold', sans-serif;
    margin: 12px 0;
    color: #EBEEEE;
    display: inline-block;
    float: left;
    width: 26%;
 }

 #contact input,
 #contact textarea,
 #contact select {
    padding: 18px 20px;
    color: #eee;
    background: #373233;
    margin-bottom: 42px;
    border: 0;
    outline: none;
    font-size: 15px;
    line-height: 24px;
    width: 65%;
 }

 #contact input:focus,
 #contact textarea:focus,
 #contact select:focus {
    color: #fff;
    background-color: #11ABB0;
 }

 #contact button.submit {
    text-transform: uppercase;
    letter-spacing: 3px;
    color:#fff;
    background: #0D0D0D;
    border: none;
    cursor: pointer;
    height: auto;
    display: inline-block;
    border-radius: 3px;
    margin-left: 26%;
 }

 #contact button.submit:hover {
    color: #0D0D0D;
    background: #fff;
 }

 #contact span.required {
    color: #11ABB0;
    font-size: 13px;
 }
 #message-warning, #message-success {
    display: none;
    background: #0F0F0F;
    padding: 24px 24px;
    margin-bottom: 36px;
    width: 65%;
    margin-left: 26%;
 }
 #message-warning { color: #D72828; }
 #message-success { color: #11ABB0; }
 
 #message-warning i,
 #message-success i {
    margin-right: 10px;
 }
 #image-loader {
    display: none;
    position: relative;
    left: 18px;
    top: 17px;
 }

/*  ------------------------------------------------------------------------*/
/*  Footer
/*  ------------------------------------------------------------------------*/
footer {
    padding-top: 48px;
    margin-bottom: 48px;
    color: #303030;
    font-size: 14px;
    text-align: center;
    position: relative;
}
 
footer a, footer a:visited { color: #525252; }
footer a:hover, footer a:focus { color: #fff; }

/* copyright */
footer .copyright {
    margin: 0;
    padding: 0;
}
footer .copyright li {
    display: inline-block;
    margin: 0;
    padding: 0;
    line-height: 24px;
}
.ie footer .copyright li {
    display: inline;
}
footer .copyright li:before {
    content: "\2022";
    padding-left: 10px;
    padding-right: 10px;
    color: #095153;
}
footer .copyright  li:first-child:before {
    display: none;
}

/* social links */
footer .social-links {
    margin: 18px 0 30px 0;
    padding: 0;
    font-size: 30px;
}
footer .social-links li {
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-left: 42px;
    color: #F06000;
}

footer .social-links li:first-child { margin-left: 0; }

/* Go To Top Button */
#go-top {
    position: absolute;
    top: -24px;
    left: 50%;
    margin-left: -30px;
}
#go-top a {
    text-decoration: none;
    border: 0 none;
    display: block;
    width: 60px;
    height: 60px;
    background-color: #525252;

    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;

    color: #fff;
    font-size: 21px;
    line-height: 60px;
    border-radius: 100%;
}
#go-top a:hover { background-color: #0F9095; }